.slider {
  width: 40px;
  height: 20px;
  cursor: pointer;
  background-color: var(--light-bg-color);
  transition: all .4s;
  inset: 0;
}

.slider:before {
  content: "";
  height: 16px;
  width: 16px;
  background-color: var(--switch-on-color);
  transition: all .4s;
  position: absolute;
  top: 2px;
  left: 1px;
}

input:checked + .slider {
  background-color: var(--switch-on-color);
}

input:focus + .slider {
  box-shadow: 0 0 1px var(--switch-on-color);
}

input:checked + .header__slider:before {
  background-color: var(--light-bg-color);
  -webkit-transform: translateX(26px);
  -ms-transform: translateX(26px);
  transform: translateX(20px);
}

.slider.round {
  border-radius: 20px;
  flex: none;
  order: 1;
}

input:checked + .header__slider.header__slider-round {
  border: .6px solid var(--light-bg-color);
}

.slider.round:before {
  color: var(--light-bg-color);
  border-radius: 50%;
}

